.customize-rcb-preset {
    background: white;
    border: 1px solid #e2e2e2;
    padding: 20px;
    cursor: pointer;
    transition: all 200ms ease-out;
    position: relative;
    margin-bottom: -4px;

    span {
        opacity: 0.8;
    }

    .btn-apply {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 120px;
        font-size: 16px;
        line-height: 20px;
        margin: -22px 0 0 -60px;
        text-align: center;
        padding: 10px 0;
        background: #008ec2;
        color: white;
        font-weight: bold;
        border: 2px solid rgba(255, 255, 255, 0.9);
    }

    &:hover {
        background: #008ec2;
        color: rgba(255, 255, 255, 0.5);
        box-shadow: 0px 2px 5px #cbcbcb;

        .btn-apply {
            display: block;
        }
    }
}
