// TODO: class prefix from env variable?

// Cards within radio button group
.rcb-antd-radio-button-card > label {
    height: auto !important;
    padding: 0;

    .rcb-antd-card {
        background: transparent;

        .rcb-antd-card-cover {
            margin: 5px;
        }
    }

    &.rcb-antd-radio-button-wrapper-checked:not(.rcb-antd-radio-button-wrapper-disabled) {
        .rcb-antd-card-meta-title,
        .rcb-antd-card-meta-description {
            color: white;
        }
    }
}

// Sticky footer for forms
.rcb-form-sticky-submit {
    position: sticky;
    bottom: 0;
    background: #f1f1f1;
    border-top: 1px solid #d2b798;
    margin-top: 10px;
    padding: 20px 0;
    z-index: 4;
}

.rcb-antd-modal-root {
    .rcb-form-sticky-submit {
        background: white;
        margin-bottom: 0;
    }
}

// Disable GIT-like view in diff2html module
.react-code-diff-lite {
    .d2h-file-header,
    .d2h-diff-tbody > tr:first-child {
        display: none;
    }
}

// See https://stackoverflow.com/a/58399650/5506547
.rcb-modal-mount {
    position: relative;
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: center center;

    .rcb-antd-modal-mask,
    .rcb-antd-modal-wrap {
        position: absolute;
    }

    .rcb-antd-modal-mask {
        background: none;
    }

    .rcb-antd-modal-close,
    .rcb-antd-modal-footer > button:not(.rcb-antd-btn-primary) {
        display: none !important;
    }
}

.rcb-antd-preset-card {
    width: 210px;
    display: inline-block;
    padding: 10px;
    margin: 5px;
    vertical-align: top;

    .rcb-antd-card-cover {
        height: 173px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .rcb-antd-card-body {
        padding: 20px 5px 10px;
    }
}
