@import "~antd/lib/typography/style/index";
@import "~antd/lib/collapse/style/index";
@import "~antd/lib/slider/style/index";

// Fix border sizing for all available elements
[class*="rcb-antd-"] {
    *,
    *::before,
    *::after {
        box-sizing: border-box;
    }
}

// Inherit color for badges (e.g. Router link seems always "active")
.rcb-antd-badge {
    color: inherit;
}

.rcb-antd-tabs-nav::before {
    border-color: #e2e2e2 !important;
}

.rcb-antd-list-item-meta-content {
    width: auto !important;
}

// Fix select inputs in WordPress backend
.rcb-antd-select-single:not(.rcb-antd-select-customize-input) .rcb-antd-select-selector {
    box-sizing: border-box;
}

.rcb-antd-select-item {
    line-height: 2;
}

// https://stackoverflow.com/a/9793669/5506547
.rcb-antd-select.rcb-antd-select-ellipses-left .rcb-antd-select-selection-item {
    direction: rtl;
}

// Reset https://git.io/JLyJJ
.rcb-antd-input {
    padding: 4px 11px !important;
    line-height: 1.5715 !important;
    min-height: auto !important;
    box-shadow: none !important;
    border-radius: 2px !important;
    border: 1px solid #d9d9d9 !important;
    outline: none !important;
}

.rcb-antd-card-meta-description {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.rcb-antd-card-actions {
    margin-top: 10px;

    a {
        text-decoration: underline;
    }
}

// Fix select dropdowns and messages in modals
.rcb-antd-select-dropdown,
.rcb-antd-message,
.rcb-antd-image-preview-mask,
.rcb-antd-image-preview-wrap,
.rcb-antd-dropdown {
    z-index: 105000000;
}

// Fix modal, popver and tooltip
.rcb-antd-modal-mask,
.rcb-antd-modal-wrap {
    z-index: 99999999;
}
.rcb-antd-tooltip {
    z-index: 100000079;
}
.rcb-antd-popover {
    z-index: 100000029;
}

.rcb-antd-image-preview-operations {
    top: 32px;
}

// Fix modal height and body overflow
.rcb-antd-modal-body {
    max-height: calc(100vh - 55px - 200px);
    overflow-y: auto;
}

// Fix forms.css to be compatible with antd checkboxes
input[type="checkbox"].rcb-antd-checkbox-input:checked::before {
    display: none !important;
}

.rcb-antd-checkbox-input {
    opacity: 0 !important;
}

@ant-prefix: rcb-antd;