@import "~@devowl-wp/react-cookie-banner-admin/src/styles";

#real-cookie-banner-component .nav-tab-wrapper {
    padding-right: 100px;
}

.rcb-nav-logo {
    float: right;
    width: 70px;
    height: 70px;
    background: white;
    padding: 5px 10px;
    border: 1px solid #ececec;
    box-shadow: 0px 1px 5px #e6e6e6;
}

.rcb-config-content {
    padding: 20px 0;
}

.rcb-antd-tabs {
    overflow: initial;
}

.rcb-antd-list-item {
    background: white;
    padding: 15px;

    .rcb-antd-badge-count {
        min-width: 9px;
    }
}

.rcb-antd-list-item-meta-title {
    margin: 0 0 4px 0;
}

.rcb-antd-form-item-label {
    white-space: break-spaces;
}

.rcb-config-footer {
    text-align: center;
    padding: 20px 0;
    font-weight: lighter;
    font-size: 11px;
    margin-top: 20px;
    opacity: 0.8;
    transition: linear opacity 500ms;

    &:hover {
        opacity: 1;

        img {
            filter: grayscale(0%);
        }
    }

    img {
        height: 40px;
        width: auto;
        display: block;
        margin: 10px auto;
        filter: grayscale(100%);
        transition: linear filter 500ms;
    }
}

// always hide checkbox hover state as it is broken in WP dashboard
.rcb-antd-checkbox-checked::after {
    display: none !important;
}

.rcb-dashboard-card-focus:hover {
    opacity: 1 !important;
}

.rcb-transparent-bg {
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAKElEQVQoU2P8////fwY08ObNG3QhBsahoPD169cYnhEREcH0zBBQCAAyhjW3tUHoCgAAAABJRU5ErkJggg==)
        repeat;
    padding: 20px;
}

.rcb-pro-modal {
    text-align: center;

    .rcb-antd-modal-footer {
        text-align: center;
    }

    .rcb-antd-modal-body {
        padding: 0 24px;

        img {
            max-width: 90%;
            max-height: 500px;
            height: auto;
        }
    }
}

.rcb-antd-tooltip a {
    color: white;
}

@media screen and (max-width: 540px) {
    #real-cookie-banner-component .nav-tab-wrapper {
        padding-right: 0px !important;
    }

    .rcb-nav-logo {
        display: none !important;
    }
}

@media screen and (max-width: 782px) {
    .rcb-antd-tabs-extra-content {
        .button {
            margin: 0px !important;
        }
    }
}
